<template>
    <div class="main_container">
        <div class="title-div" v-pre>1.  {{}}</div>
        <hr>
        <h1>{{ msg }}</h1>

        <div class="title-div" v-pre>2. v-if  v-show</div>
        <hr>
        <p v-if="isShow"> v-if </p>  
        <p v-show="isShow"> v-show </p>

        <div class="title-div" v-pre>3. v-bind  :</div>
        <hr>
        <img :src="adsImagePath" :width="width" height="100">
        <!-- <img src="@/assets/test.png" width="100" height="100"> -->

        <button :disabled="isBtnDisabled">Button</button>

        <div v-bind="objectOfAttrs">v-bindidclass</div>

        <p :class="isDynamicClass">class</p>
        <p :class="className">classNamecss</p>
        <p :style="{ color: dynamicColor }">style</p>
        <p :style="myDynamicStyle">style</p>

        <div class="title-div" v-pre>4. v-for</div>
        <hr>
        <ul>
            <li v-for="item in items" :key="item.id">{{ item.name }}</li>
        </ul>
        <div :class="rowOrColumnClass">
            <span :class="rowOrColumnItemClass" v-for="(value, index) in items2" :key="index">{{ value }}</span>
        </div>
        <div class="v-for-buttons">
            <button @click="clickedReverseBtn">Reverse</button>
            <button @click="clickedSliceBtn">Slice</button>
            <button @click="clickedChangeBtn">{{ isChanged ? "a to i" : "i to a" }}</button>
            <button @click="clickedChangeRowOrColumnBtn">{{ rowOrColumnBtnName }}</button>
        </div>

        <div class="title-div" v-pre>5. v-on  @</div>
        <hr>
        <button @click="changeMsg"></button>
        <button @click="handleClick"></button>

        <p></p>
        <a @click="clickedToGo" href="http://www.baidu.com"></a>
        <br>
        <a @click="clickedToGo2(2, $event)" href="http://www.baidu.com"></a>

        <div class="title-div" v-pre>6. computed</div>
        <hr>
        <div>
            <p>: {{ msg }}</p>
            <p>: {{ reversedMessage }}</p>
            <p>: {{ greeting }}</p>
        </div>

        <div class="title-div" v-pre>7. watch</div>
        <hr>
        <div>
            <p>: {{ question }}</p>
            <p>: {{ answer }}</p>
        </div>

        <div class="title-div" v-pre>8. v-textv-html</div>
        <hr>
        <div>
            <p>Using text interpolation: {{ rawHtml }}</p>
            <p v-text="rawHtml"></p>
            <p v-html="rawHtml"></p>
        </div>
        <div class="title-div" v-pre>9. </div>
        <hr>
        <div>
            <div @click="fatherClick">
                <ul>
                    <li><button @click.stop="stopClick">.stop,fatherClick</button></li>
                    <li><a href="http://www.baidu.com" @click.prevent="stopClick">.prevent</a></li>
                    <li><button @click.once="onceClick" @click.stop="">.once, </button></li>
                    <li>ESC: <input type="text" @keydown.enter="enterFun" @keydown.esc="escFun" /></li>
                </ul>
            </div>
        </div>
        <div class="title-div" v-pre>10. v-model</div>
        <hr>
        <div>
            <span>: </span>
            <input type="text" v-model="username" />
        </div>
        <div>
            <span>: </span>
            <input type="password" v-model="password" />
        </div>
        <div>
            <span>: </span>
            <input type="checkbox" v-model="hobbies" value="workout" @change="handleCheckboxChange" />
            <input type="checkbox" v-model="hobbies" value="reading" @change="handleCheckboxChange" />
            <input type="checkbox" v-model="hobbies" value="game" @change="handleCheckboxChange" />
        </div>
        <div>
            <span>: </span>
            <input type="radio" v-model="gender" value="male" @change="handleRadioChange"/>
            <input type="radio" v-model="gender" value="female" @change="handleRadioChange"/>
        </div>
        <div>
            <span>: </span>
            <input type="text" v-model.number="praize" />{{ praize }}
        </div>
        <div>
            <span>: </span>
            <input type="text" v-model.trim="comment" />
        </div>

        <div>
            <span>:</span>
            <input type="text" placeholder="handle input text" v-model="comment" @keyup.enter="handleEnter"
                @blur="handleEnter" />
        </div>

        <div class="title-div" v-pre>11. </div>
        <hr>
        <div>
            <span></span>
            <input type="checkbox" v-model="selectAll" />
            <ul>
                <li v-for="(val, index) in selectionArray" :key="index">
                    <input type="checkbox" v-model="val.selected" />
                    <span>{{ val.name }}</span>
                </li>
            </ul>
        </div>

        <div class="title-div" v-pre>12. v-ifv-else</div>
        <div v-if="isShow">isShow==true</div>
        <div v-else></div>
        <hr>

        <div class="title-div" v-pre>13. v-once</div>
        <div v-once>v-once, msg, :{{ msg }}</div>
        <hr>
        <div class="title-div" v-pre>14. v-pre</div>
        <div v-pre>v-pre, vue: {{ msg }}</div>
        <hr>
        <div class="title-div" v-pre>15. Attribute</div>
        <div :title="msg"></div>
        <div :class="{ beRed: isRed }" @click="toggleRed">class, </div>
        <div :style="{ color: dynamicColor }" @click="toggleColor">style,</div>
        <hr>
        <div class="title-div" v-pre>16. </div>
        <button @click="show = !show">{{ show ? "Hide List" : "Show List" }}</button>
        <button @click="pushNumber">Push Number</button>
        <button @click="popNumber">Pop Number</button>
        <button @click="reverseList">Reverse List</button>
        <ul v-if="show && list.length">
            <li v-for="(value, index) in list" :key="index">{{ value }}</li>
        </ul>
        <div v-else-if="show === false">The list is hidden.</div>
        <div v-else-if="list.length == 0">The list is empty.</div>
        <hr>
        <div class="title-div" v-pre>17. &lt;template&gt; v-if</div>
        <button @click="toggleShowTemplate"> {{ showTemplate ? "Hide" : "Show" }} the template below</button>
        <template v-if="showTemplate">
            <h3>Title</h3>
            <p>Paragraph 1</p>
            <p>Paragraph 2</p>
        </template>
        <hr>
        <div class="title-div" v-pre>18. </div>
        <h3>Text Input</h3>
        <input v-model="text" placeholder="Edit me">
        <div>{{ text }}</div>
        <h3>Checkbox</h3>
        <input type="checkbox" v-model="checked">
        <label>Checked: {{ checked }}</label>
        <h3>Multi Checkbox</h3>
        <input type="checkbox" value="Jack" v-model="checkedNames">
        <label>Jack</label>
        <input type="checkbox" value="Lily" v-model="checkedNames">
        <label>Lily</label>
        <input type="checkbox" value="Lucy" v-model="checkedNames">
        <label>Lucy</label>
        <div>Checked names: {{ checkedNames }}</div>
        <h3>Radio</h3>
        <input type="radio" value="One" v-model="picked">
        <label>One</label>
        <input type="radio" value="Two" v-model="picked">
        <label>Two</label>
        <div>Picked: {{ picked }}</div>
        <h3>Select</h3>
        <select v-model="selected">
            <option disabled value="">Please select one</option>
            <option>A</option>
            <option>B</option>
            <option>C</option>
        </select>
        <div>Selected: {{ selected }}</div>
        <h3>Multi Select</h3>
        <select v-model="multiSelected" multiple style="width: 200px;">
            <option>A</option>
            <option>B</option>
            <option>C</option>
        </select>
        <div>Selected: {{ multiSelected }}</div>
        <hr>

        <div class="title-div" v-pre>20. v-for</div>
        <ul>
            <li v-for="(value, key, index) in myObject" :key="index">
                {{ index }}. {{ handleKeyInVFor(key) }}: {{ value }}
            </li>
        </ul>
        <hr>


        <div class="title-div" v-pre>21. </div>
        <form @submit.prevent="addTodo">
            <input v-model="newTodo" required placeholder="New todo">
            <button>Add Todo</button>
        </form>
        <ul>
            <li v-for="todo in filteredTodos" :key="todo.id">
                <input type="checkbox" v-model="todo.done">
                <del v-if="todo.done">{{ todo.id }}. {{ todo.text }}</del>
                <span v-else>{{ todo.id }}. {{ todo.text }}</span>
                <button @click="removeTodo(todo)">X</button>
            </li>
        </ul>
        <button @click="clickedHideCompletedButton">{{ this.hideCompleted ? "Show all" : "Hide completed" }}</button>
        <hr>
        <div class="title-div" v-pre>22. </div>
        <p ref="pElementRef">hello</p>
        <div class="title-div" v-pre>23. </div>
        <p>Todo id: {{ todoId }}</p>
        <button @click="todoId++" :disabled="!todoData">Fetch next todo</button>
        <p v-if="!todoData">Loading...</p>
        <pre v-else>{{ todoData }}</pre>
        <hr>
        <div class="title-div" v-pre>24.  props </div>
        <div>{{ msgInProps || 'No props passed yet' }}</div>
        <div class="title-div" v-pre>25.  Emits </div>
        <button @click="handleEmit">Emit event</button>
    </div>
</template>

<style>
.main_container {
    margin: 20px;
}

div {
    text-align: left;
}

.title-div {
    margin-top: 20px;
    font-family: Arial, sans-serif;
    font-size: 20px;
    font-weight: bold;
    text-align: left;
    color: black;
}

/* ul {
    list-style-type: none;
    padding: 0px; 
} */

li {
    line-height: 2;
}

.green_class {
    color: green;
}

.v-for-row {
    display: flex;
    flex-direction: row;
}

.v-for-row-item {
    font-size: 32px;
    font-weight: bold;
    margin-left: 10px;
    /*  */
    background-color: pink;
}

.v-for-column {
    display: flex;
    flex-direction: column;
}

.v-for-column-item {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 10px;
    /*  */
    background-color: blue;
}


.v-for-buttons button {
    margin-right: 10px;
    margin-top: 10px;
}

#container {
    color: red;
}

.beRed {
    color: red;
}
</style>

<script>
import testImagePath from '@/assets/images/logo.png';

let todoIndex = 0;

export default {
    mounted() {
        console.log('MyHelloWorld has been mounted!');
        this.$refs.pElementRef.textContent = 'mounted! 666'
        this.fetchData()

    },
    created() {
        console.log('MyHelloWorld has been created!');

        // 
        this.$emit('response', 'Hello from child')
    },
    props: {
        msgInProps: String
    },
    emits: ['response'],
    data() {
        return {
            msg: "Hello, Vue!",
            name: "Vue",
            isShow: true,
            adsImagePath: testImagePath, // '@/assets/ads.jpg',, import
            width: 50,
            items: [
                { id: 1, name: "item1" },
                { id: 2, name: "item2" },
                { id: 3, name: "item3" }],
            items2: ["c", "o", "d", "e", "i", "s", "w", "o", "r", "l", "d"],
            question: '',
            answer: 'I cannot give you an answer until you ask a question!',
            rawHtml: '<span style="color:red">This will replace the existing content.</span>',
            isBtnDisabled: true,
            objectOfAttrs: {
                id: 'container',
                class: 'wrapper'
            },
            isDynamicClass: {
                green_class: true //green_classclassgreen_classcss, truecss
            },
            className: "beRed",
            rowOrColumnClass: "v-for-row",
            rowOrColumnItemClass: "v-for-row-item",
            rowOrColumnBtnName: "Column",
            dynamicColor: "orange",
            myDynamicStyle: { "font-size": "40px", "color": "purple", "text-align": "left" },
            username: "",
            password: "",
            hobbies: [],
            gender: "",
            praize: 0,
            comment: "",
            isSliced: false,
            isChanged: false,
            selectionArray: [{ name: "", selected: false }, { name: "", selected: false }, { name: "", selected: false }],
            isRed: false,
            show: true,
            list: [1, 2, 3],
            showTemplate: false,
            text: "",
            checked: false,
            checkedNames: ["Lily"],
            picked: "Two",
            selected: "B",
            multiSelected: [],
            myObject: {
                title: 'How to do lists in Vue',
                author: 'Jane Doe',
                publishedAt: '2016-04-10'
            },
            newTodo: '',
            todos: [
                { id: todoIndex++, text: 'Learn HTML', done: false },
                { id: todoIndex++, text: 'Learn JavaScript', done: false },
                { id: todoIndex++, text: 'Learn Vue', done: false }
            ],
            hideCompleted: false,
            todoId: 1,
            todoData: null,
            greetingMsg: '',
        };
    },
    computed: {
        reversedMessage() {
            return this.msg.split('').reverse().join('');
        },
        // greeting() {
        //     return `Hello, ${this.name}!`;
        // },
        greeting: {
            set(value) {
                console.log("greeting: " + this.value)
                this.greetingMsg = value
            },
            get() {
                return `${this.greetingMsg}!`;
            }
        },
        selectAll: {
            set(value) {
                console.log("selectAll.set:" + value)
                this.selectionArray.forEach(obj => { obj.selected = value });
            },
            get() {
                let selected = true
                let outsideVar = 0
                this.selectionArray.forEach((obj, index) => {
                    console.log(index + ". obj.selected is: " + obj.selected)
                    selected = (obj.selected === true);
                    if (obj.selected) {
                        outsideVar++;
                    }
                });

                console.log("selected: " + selected);
                console.log("outsideVar: " + outsideVar);
                return outsideVar >= 3
                //return this.selectionArray.every(obj => {obj.selected === true});
            }
        },
        filteredTodos() {
            return this.hideCompleted ? this.todos.filter(t => t.done == false) : this.todos;
        }
    },
    watch: {
        //  question 
        question(newValue, oldValue) {
            console.log("question: newValue: " + newValue);
            console.log("question: oldValue: " + oldValue);

            this.answer = '...';
            this.getAnswer();
        },

        todoId(newValue) {
            console.log("todoId has changed to " + newValue);

            this.fetchData();
        }
    },
    methods: {

        //this
        handleEmit() {
            console.log("handleEmit");
            this.$emit('response', 'Hello from child')
        },
        scrollToBottom() {
            window.scrollTo({
                top: document.documentElement.scrollHeight,
                behavior: 'smooth' // 
            });
        },

        changeMsg() {
            this.msg = "Hello, World!";
            this.question = "Is this a question?"
            this.greetingMsg = "Hello, Vue.js";
        },


        handleClick() {
            alert('');
            console.log(this.hobbies);
            console.log(this.gender);
        },

        getAnswer() {
            this.answer = 'Yes, it is and this is my answer.';
        },

        clickedToGo(e) {
            e.preventDefault();
        },

        clickedToGo2(id, e) {
            //console.log(id);
            //console.log(e);
            e.preventDefault();
        },

        fatherClick() {
            console.log("fatherClick");
        },

        stopClick(e) {
            console.log("stopClick");

            e.preventDefault();
        },

        onceClick() {
            console.log("onceClick");
        },

        enterFun() {
            console.log("enterFun");
        },

        escFun() {
            console.log("escFun");
        },

        clickedReverseBtn() {
            this.items2.reverse();
        },

        clickedSliceBtn() {
            if (this.isSliced == false) {
                let array = this.items2.slice(0, 4);
                this.items2 = array;
                this.isSliced = true
            } else {
                this.items2 = ["c", "o", "d", "e", "i", "s", "w", "o", "r", "l", "d"];
                this.isSliced = false
            }
        },

        clickedChangeBtn() {
            if (this.isChanged) {
                this.isChanged = !this.isChanged;
                this.items2[4] = "i"
            } else {
                this.isChanged = !this.isChanged;
                this.items2[4] = "a"
            }
        },

        clickedChangeRowOrColumnBtn() {
            if (this.rowOrColumnBtnName == "Column") {
                this.rowOrColumnClass = "v-for-column";
                this.rowOrColumnItemClass = "v-for-column-item";
                this.rowOrColumnBtnName = "Row";
            } else {
                this.rowOrColumnClass = "v-for-row";
                this.rowOrColumnItemClass = "v-for-row-item";
                this.rowOrColumnBtnName = "Column";
            }
        },

        handleCheckboxChange() {
            console.log("hobbies: " + this.hobbies);
        },
        
        handleRadioChange() {
            console.log("gender: " + this.gender);
        },

        handleEnter() {
            this.comment = this.comment.trim();
        },

        toggleRed() {
            this.isRed = !this.isRed;
        },

        toggleColor() {
            this.dynamicColor = this.dynamicColor === 'orange' ? 'blue' : 'orange';
        },

        popNumber() {
            this.list.pop();
        },

        pushNumber() {
            this.list.push(this.list.length + 1);
        },

        reverseList() {
            this.list.reverse();
        },

        toggleShowTemplate() {
            this.showTemplate = !this.showTemplate;

            // Vue.nextTick  DOM 
            // this.$nextTick(() => {
            //     this.scrollToBottom();
            // });

        },

        handleKeyInVFor(key) {
            var str = String(key);
            if (str.length > 0) {
                return str.charAt(0).toUpperCase() + str.slice(1);
            }

            return str;
        },

        addTodo() {
            if (this.newTodo.length > 0) {
                if (this.todos.some(t => t.text === this.newTodo)) {
                    alert("Todo already exists!");
                    return;
                }
                
                todoIndex = this.todos.length
                this.todos.push({ id: todoIndex, text: this.newTodo, done: false})
            }
        },

        removeTodo(todo) {
            this.todos = this.todos.filter(t => t.id != todo.id)
        },

        clickedHideCompletedButton() {
            this.hideCompleted = !this.hideCompleted;
        },

        async fetchData() {
            this.todoData = null
            const res = await fetch(`https://jsonplaceholder.typicode.com/todos/${this.todoId}`)
            this.todoData = await res.json()
        }
    }
};
</script>